<template lang="">
    <div class="interesting">
    <div class="container">
      <div class="interesting-title">
        <h2>{{titleBlogArticle}}</h2>
        <router-link class="interesting-btn" to="/blog">all articles</router-link>
      </div>
      <div class="interesting-main">
        <swiper
          :slidesPerView="'auto'"
          :space-between = "20"
          :navigation="true"
          :modules="modules"
          class="mySwiper3"
        >
          <swiper-slide class="interesting-swiper" v-for="(blogCart, index) in blogCarts" :key="index">
            <div class="interesting-slide_content" @click="cartClick(blogCart.id)">
              <div class="interesting-slide_img1">
                <img :src="blogCart.image">
              </div>
              <div class="interesting-slide_main">
                <p>10 May, 2020</p>
                <h5>{{blogCart.titleCart}}</h5>
              </div>
            </div>
          </swiper-slide>
          <!-- <swiper-slide class="interesting-swiper">
            <router-link class="interesting-slide" to="/article">
              <div class="interesting-slide_content">
                <div class="interesting-slide_img2"></div>
                <div class="interesting-slide_main">
                  <p>10 May, 2020</p>
                  <h5>How fast can you order a car in Canada?</h5>
                </div>
              </div>
            </router-link>
          </swiper-slide>
          <swiper-slide class="interesting-swiper">
            <router-link class="interesting-slide" to="/article">
              <div class="interesting-slide_content">
                <div class="interesting-slide_img3"></div>
                <div class="interesting-slide_main">
                  <p>10 May, 2020</p>
                  <h5>How to save on buying a car? Why is it better to buy from us</h5>
                </div>
              </div>
            </router-link>
          </swiper-slide> -->
        </swiper>
