    </div>
  </div>
</template>
<script>

// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from 'swiper/vue';

// Import Swiper styles
import 'swiper/css';

import "swiper/css/navigation";

//import "./style.css";

import { Navigation } from "swiper";

export default {

  props:{
    titleBlogArticle: String,
  },

    components: {
      Swiper,
      SwiperSlide,
    },
    setup() {
      return {
        modules: [Navigation],
      };
    },

    computed: {
        blogCarts(){
            return this.$store.getters.blogCarts
        },
        visibleList: function(){
          return this.blogCarts.slice(0, 3);
        }
    },

    methods:{
        cartClick(id){
            this.$router.push({name: 'Article', query: {'blog': id}})
        }
    },
    
}
</script>
